import React from 'react';
import styled from 'styled-components';

const SectionContainer = styled.section`
  padding: ${({ $fullHeight }) => $fullHeight ? '0' : '6rem 0'};
  min-height: ${({ $fullHeight }) => $fullHeight ? '100vh' : 'auto'};
  position: relative;
  overflow: hidden;
`;

const Section = ({ id, children, fullHeight }) => {
  return (
    <SectionContainer id={id} $fullHeight={fullHeight}>
      {children}
    </SectionContainer>
  );
};

export default Section; 